import { KeyboardModule } from './../../../../../keyboard/keyboard.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BookingComponent } from './booking.component';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { CartComponent } from './cart/cart.component';
import { DetailComponent } from './detail/detail.component';
import { CanLeavePage } from 'src/app/cafe/desktop/canLeavePage';
import { SearchTableBookingModule } from './components/search-table-booking/search-table-booking.module';
const routes: Routes = [
  {
    path: '',
    component: BookingComponent
  },
  {
    path: "cart",
    loadChildren: () => import("./cart/cart.module").then(m => m.CartModule)
  },
  {
    path: "detail",
    component: DetailComponent
  }
]

@NgModule({
  declarations: [BookingComponent, DetailComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    SearchTableBookingModule,
    KeyboardModule
  ]
})
export class BookingModule { }
