import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhQueryCafe, VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-search-table-booking',
  templateUrl: './search-table-booking.component.html',
  styleUrls: ['./search-table-booking.component.scss']
})
export class SearchTableBookingComponent implements OnInit {
  @Input('visibleSearchTable') visibleSearchTable: boolean = false;
  @Input('status') status: boolean | null = null;
  @Output('closeAndReceiveValueSearch') closeAndReceiveValueSearch = new EventEmitter<any>()
  listTab: Array<any> = [];
  indexTab: number = 0
  areaID: string;
  tables: Array<any> = [];
  heightWindow: string = window.innerHeight * 0.65 + 'px'
  constructor(
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    private vhAlgorithm:VhAlgorithm
  ) { }

  ngOnInit(): void {
    this.getArea()
  }
  getArea() {
    this.listTab = this.vhQueryCafe.getlocalAreas().filter(item => item._id != 'go_home' && item.status && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
    if (this.listTab.length) {
      this.areaID = this.listTab[0]['_id']
      this.getTable()
    }
  }
  getTable(id?) {
    if (id) this.areaID = id
    this.tables = this.vhQueryCafe.getlocalTables().filter(item => this.status != null ? (item.id_area == this.areaID && item.status == this.status) : item['id_area'] == this.areaID)
    this.tables = this.vhAlgorithm.sortVietnamesebyASC(this.tables,'name');
  }
  handleOk(table) {
    this.closeAndReceiveValueSearch.emit(table)
  }
  cancel() {
    this.closeAndReceiveValueSearch.emit(false)
  }
}
